@import '../../styles/main.scss';

.section {
  margin-top: 140px;
  text-align: center;
  margin-bottom: 140px;
}

.container {
  max-width: 328px;
  margin: 0 auto;

  @media screen and (min-width: $tablet) {
    max-width: 704px;
  }

  @media screen and (min-width: $desktop) {
    max-width: 904px;
  }
}

.title {
  font-size: 40px;
  font-weight: 400;
  line-height: 40px;
  text-align: center;
}



.loader {
  display: block;
  margin: 0 auto;
  width: 50px;
  height: 50px;
  margin-bottom: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}