.modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 30px;
  
}

.modalContent {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: #fff;
  padding: 1rem;
  border-radius: 10px;
  text-align: center;
  position: relative;
  min-width: 300px;
  max-width: 600px;
  min-height: 300px;
  max-height: 600px;
  padding: 40px;
}


.icon {
  cursor: pointer;
  width: 20px;
  height: 20px;
  position: absolute;
  right: 16px;
  top: 16px;
}


.cross {
  width: 100%;
  height: 100%;
}

.errorImg {
  width: 50px;
  height: 50px;
  color: rgba(0, 0, 0, 0.5) ;
  margin-bottom: 20px;
}

.text {
  font-size: 30px;
  line-height: 30px;
}

.successImg {
  width: 60%;
  height: 60%;
  padding: 20px;
}